body {
  margin: 0;
  position: relative;
  /*background-color: rgb(32, 202, 172);*/
}

.root {
  font-family: sans-serif;
  text-align: center;
  /*padding-top: 120px;*/
}

.wrapper {
  height: 1080px;
  /*width: 100%;*/
  background-repeat: repeat-y;
  background-size: cover;
  background-position: center 0;
  position: relative;
}

.wrapper1 {
  background-image: url(./assets/images/Slide_1_sheme_2.png);
}

.wrapper2 {
  background-image: url(./assets/images/Slide_2_sheme_2.png);
}

.wrapper3 {
  background-image: url(./assets/images/Slide_3_sheme_2.png);
}

.target {
}

.ball {
  height: 40px;
  width: 40px;
  background-color: red;
  border-radius: 50%;
  margin-left: auto;
  margin-right: auto;
  opacity: 0.5;
}

.ball.isFixed {
  position: fixed;
  left: 50%;
  top: 100px;
  z-index: 1;
}

.spacer {
  height: 70vh;
}

.trigger {
  height: 1px;
  background-color: #000;
}


@media (max-width: 640px) {
    .wrapper {
        height: 506px;
    }
    .ball {
        height: 20px;
        width: 20px;
    }
}